.cart{
    position: relative;
    max-width: 1127px;
    border: 1px solid #ccc;
    transform: scaleY(0.98);
}

.amount span{
    color: crimson;
    padding: 0 20px;
}
.amount button{
    width: 40px;
    height: 40px;
    border: 1px solid #777;

}
.delete{
    position: absolute;
    top: 0;
    right: 5px;
    color: crimson;
    font-weight: 900;
    cursor: pointer;
}
.total{
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;

}
.total h3{
    color: crimson;
}